<template>
  <el-row>
    <el-col :span="2"></el-col>
    <el-col :span="11">
      <input v-if="Editable"  type="text" v-model="todo">
      <span v-else>{{todo}}</span>
    </el-col>
    <el-col :span="2">
      <input v-if="Editable"  type="number" v-model="percent">
      <span v-else>{{ percent }}</span>
    </el-col>
    <el-col :span="3">
      <el-button type="primary" icon="el-icon-edit" @click="editableChange">编辑</el-button>
    </el-col>
    <el-col :span="3">
      <el-button type="danger" icon="el-icon-delete" @click="delete_todo()">删除</el-button>
    </el-col>
    <el-col :span="3">
      <el-button type="success" icon="el-icon-delete" @click="ok">完成</el-button>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Item",
  props: ['id','todo', 'percent','deleteTodo1'],
  data(){
    return{
      Editable:false
    }
  },
  methods:{
    ok(){
      this.$emit('complete_todo', this.percent);
      this.$emit('deleteTodo',this.id)

    },
    delete_todo(){
      this.$emit('deleteTodo',this.id)
    },
    editableChange(){
      this.Editable=!this.Editable;
    }
  }
}
</script>

